//BaseUiComponents.js
import React from 'react';

export const StyledInput = ({ gameState, updateGameState, statKey, placeholder }) => {
  return (
    <input
      type="text"
      value={gameState.playerMonster[statKey]}
      onChange={(e) => updateGameState({
        playerMonster: {
          ...gameState.playerMonster,
          [statKey]: e.target.value
        }
      })}
      placeholder={placeholder}
      className="styled-input"
    />
  );
};

