import React, { useState, useEffect } from 'react';
import { 
  Card, 
  CardContent, 
  Typography, 
  Button, 
  Select, 
  MenuItem, 
  FormControl, 
  InputLabel,
  TextField,
  Box,
  Paper
} from '@mui/material';

// Event System
const eventBus = {
  events: {},
  on(event, callback) {
    this.events[event] = this.events[event] || [];
    this.events[event].push(callback);
  },
  emit(event, payload) {
    if (this.events[event]) {
      this.events[event].forEach(callback => callback(payload));
    }
  }
};

// Monster Class
class Monster {
  constructor(name, config) {
    this.name = name;
    this.hp = config.hp || 100;
    this.str = config.str || 10;
    this.dex = config.dex || 5;
    this.int = config.int || 5;
    this.wis = config.wis || 5;
    this.con = config.con || 10;
    this.actions = config.actions || [];
    this.currentHp = this.hp;
  }

  takeDamage(amount) {
    this.currentHp -= amount;
    if (this.currentHp < 0) this.currentHp = 0;
  }

  isAlive() {
    return this.currentHp > 0;
  }
}

// Action Class
class Action {
  constructor(name, config) {
    this.name = name;
    this.trigger = config.trigger || 'newTurn';
    this.condition = config.condition || (() => true);
    this.effect = config.effect || (() => {});
  }
}

// Create Functions
const monsters = [];

function createMonster(name, config) {
  const newMonster = new Monster(name, {
    hp: 100, str: 10, dex: 5, int: 5, wis: 5, con: 10,
    actions: [],
    ...config
  });
  monsters.push(newMonster);
  return newMonster;
}

function createAction(name, config) {
  return new Action(name, {
    trigger: 'newTurn',
    condition: () => true,
    effect: () => {},
    ...config
  });
}

// Battle Simulator
function simulateBattle(monster1, monster2) {
  let round = 0;
  let battleLog = "";
  monster1.currentHp = monster1.hp;
  monster2.currentHp = monster2.hp;

  while (monster1.isAlive() && monster2.isAlive()) {
    round++;
    battleLog += `\n--- Round ${round} --- \n`;

    eventBus.emit('newTurn', { round, monster1, monster2 });

    battleLog += processMonsterTurn(monster1, monster2, round);
    if (!monster2.isAlive()) break;

    battleLog += processMonsterTurn(monster2, monster1, round);
  }

  battleLog += `\n---\n${monster1.isAlive() ? monster1.name : monster2.name} Wins! 🏆`;
  return battleLog;
}

function processMonsterTurn(monster, opponent, round) {
  let log = "";
  for (const action of monster.actions) {
    if (action.trigger === 'newTurn' && action.condition({ round })) {
      log += executeAction(action, monster, opponent, round);
    }
  }
  return log;
}

function executeAction(action, attacker, defender, round) {
  let log = "";
  let damage = 0;
  
  if (action.effect) {
    let damageCallback = (damageAmount) => {
      damage = damageAmount;
    };
    let context = { attacker, defender, round, damageCallback };

    eventBus.emit("beforeDealDamage", context);
    action.effect(context);

    if (damage > 0) {
      eventBus.emit("dealDamage", context);
      log += `${attacker.name} 🗡️ ${defender.name} for ${damage}! `;
      defender.takeDamage(damage);
      eventBus.emit("afterDealDamage", context);
    }
  }

  log += defender.isAlive() ? "" : `${defender.name} has fallen. 💀 \n`;
  return log;
}

// Define Actions
const punchAction = createAction("Punch", {
  effect: ({ attacker, damageCallback }) => {
    damageCallback(attacker.str);
  },
});

const strongPunchAction = createAction("Strong Punch", {
  condition: ({ round }) => round % 3 === 0,
  effect: ({ attacker, damageCallback }) => {
    damageCallback(attacker.str * 2);
  }
});

const fireboltAction = createAction("Firebolt", {
  condition: ({ round }) => round % 4 === 0,
  effect: ({ attacker, damageCallback }) => {
    damageCallback(attacker.int * 2);
  }
});

const healAction = createAction("Heal", {
  condition: ({ round }) => round % 5 === 0,
  effect: ({ attacker, damageCallback }) => {
    if (attacker.currentHp < attacker.hp) {
      const healAmount = Math.min(attacker.hp - attacker.currentHp, Math.floor(attacker.wis * 1.5));
      attacker.currentHp += healAmount;
      damageCallback(-healAmount);
    } else {
      damageCallback(0);
    }
  }
});

const resistance = createAction("Resistance", {
  trigger: "beforeDealDamage",
  effect: ({ damageCallback }) => {
    damageCallback(Math.floor(damageCallback() / 2));
  }
});

const lifesteal = createAction("Lifesteal", {
  trigger: "afterDealDamage",
  effect: ({ attacker, damage }) => {
    attacker.currentHp = attacker.currentHp + (damage * 0.1);
  }
});

const dodge = createAction("Dodge", {
  trigger: "beforeDealDamage",
  condition: ({ round }) => round % 2 === 0,
  effect: ({ damageCallback }) => {
    damageCallback(0);
  }
});

// Create Monsters
createMonster("Goblin", {
  str: 8,
  dex: 7,
  actions: [punchAction, strongPunchAction]
});

createMonster("Knight", {
  str: 12,
  con: 15,
  actions: [punchAction, resistance]
});

createMonster("Mage", {
  int: 15,
  actions: [punchAction, fireboltAction, lifesteal]
});

createMonster("Priest", {
  wis: 14,
  actions: [punchAction, healAction]
});

createMonster("Rogue", {
  dex: 12,
  actions: [punchAction, dodge]
});

// Main Component
const ModBattler = () => {
  const [monster1, setMonster1] = useState("Goblin");
  const [monster2, setMonster2] = useState("Goblin");
  const [battleLog, setBattleLog] = useState("");
  const [pasteContent, setPasteContent] = useState("");
  const [error, setError] = useState("");

  const handlePaste = () => {
    try {
      if (pasteContent) {
        eval(pasteContent);
        setPasteContent("");
        setError("");
      }
    } catch (err) {
      setError(`Error: ${err.message}`);
      console.error('Error executing pasted code:', err);
    }
  };

  const handleBattle = () => {
    const selectedMonster1 = monsters.find(m => m.name === monster1);
    const selectedMonster2 = monsters.find(m => m.name === monster2);
    
    if (selectedMonster1 && selectedMonster2) {
      setBattleLog(simulateBattle(selectedMonster1, selectedMonster2));
    } else {
      setBattleLog("Error: Could not find selected monster");
    }
  };

  return (
    <Card sx={{ maxWidth: 800, mx: 'auto', mt: 4 }}>
      <CardContent>
        <Typography variant="h4" component="h1" gutterBottom align="center">
          ⚔️ Monster Battler ⚔️
        </Typography>

        <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
          <FormControl fullWidth>
            <InputLabel>Monster 1</InputLabel>
            <Select
              value={monster1}
              onChange={(e) => setMonster1(e.target.value)}
              label="Monster 1"
            >
              {monsters.map(monster => (
                <MenuItem key={monster.name} value={monster.name}>
                  {monster.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>

          <FormControl fullWidth>
            <InputLabel>Monster 2</InputLabel>
            <Select
              value={monster2}
              onChange={(e) => setMonster2(e.target.value)}
              label="Monster 2"
            >
              {monsters.map(monster => (
                <MenuItem key={monster.name} value={monster.name}>
                  {monster.name}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Box>

        <TextField
          fullWidth
          multiline
          rows={4}
          value={pasteContent}
          onChange={(e) => setPasteContent(e.target.value)}
          placeholder="Paste content here to create new monsters or actions..."
          variant="outlined"
          sx={{ mb: 2 }}
        />

        {error && (
          <Typography color="error" sx={{ mb: 2 }}>
            {error}
          </Typography>
        )}

        <Box sx={{ display: 'flex', gap: 2, mb: 3 }}>
          <Button
            variant="contained"
            color="primary"
            onClick={handleBattle}
            fullWidth
          >
            Start Battle
          </Button>
          <Button
            variant="outlined"
            onClick={handlePaste}
            fullWidth
          >
            Execute Pasted Code
          </Button>
        </Box>

        <Paper 
          variant="outlined" 
          sx={{ 
            p: 2,
            minHeight: 200,
            whiteSpace: 'pre-line',
            fontFamily: 'monospace',
            bgcolor: 'grey.50'
          }}
        >
          <div dangerouslySetInnerHTML={{ __html: battleLog || 'Battle log will appear here...' }} />
        </Paper>
      </CardContent>
    </Card>
  );
};

export default ModBattler;