//MonsterGame.js
import React, { useState, useEffect } from "react";
import './style.css';
import MonsterCreator from './MonsterCreator';
import MonsterBattle from './MonsterBattle';
import MessageBox from './MessageBox';

export class Monster {
  constructor({
    name = 'Monster',
    hp = 100,
    maxHp = 100,
    dmg = 10,
    block = 10,
    mana = 50,
    spellDmg = 50,
    spellCost = 30
  } = {}) {
    this.name = name;
    this.hp = hp;
    this.maxHp = maxHp;
    this.dmg = dmg;
    this.block = block;
    this.mana = mana;
    this.spellDmg = spellDmg;
    this.spellCost = spellCost;
  }

  attack(target) {
    const damage = Math.random() < target.block / 100 ? 0 : this.dmg;
    target.hp -= damage;
    return { damage, type: 'attack' };
  }

  castSpell(target) {
    if (this.mana >= this.spellCost) {
      this.mana -= this.spellCost;
      const damage = Math.random() < target.block / 100 ? 0 : this.spellDmg;
      target.hp -= damage;
      return { damage, type: 'spell' };
    }
    return this.attack(target);
  }

  isAlive() {
    return this.hp > 0;
  }
}

const createStartingMonster = () => {
  return new Monster({ name: 'Player Monster', hp: 100, dmg: 10 });
};



const MonsterGame = () => {
  const [message, setMessage] = useState('');
  const [activeTab, setActiveTab] = useState('creator');

  const [gameState, setGameState] = useState({
    gold: 100,
    difficulty: 1,
    playerMonster: createStartingMonster(),
    enemyMonster: new Monster({ name: 'Enemy Monster', hp: 100, dmg: 10 })
  });
  const updateGameState = (newState) => {
    setGameState((prevState) => ({ ...prevState, ...newState }));
  };
  console.log(gameState)
  return (
    <div>
      <h1>☠️ Grimdark Fantasy Battle ☠️</h1>
      <MessageBox message={message} clearMessage={() => setMessage('')} /> {/* MessageBox component */}
      
      {activeTab === 'creator' && ( <>
        <MonsterCreator gameState={gameState} updateGameState={updateGameState} setMessage={setMessage} />
        <button id="start-battle" onClick={() => setActiveTab('battle')}>🏃 Go to Battle 🏃 </button>
        </>
        
      )}
      {activeTab === 'battle' && (
        <><MonsterBattle gameState={gameState} updateGameState={updateGameState} setMessage={setMessage} setActiveTab={setActiveTab} /></>
      )}
      
      
    </div>
  );
};


export default MonsterGame;
