//MonsterCreator.js
import React, { useState } from 'react';
import MessageBox from './MessageBox'; // Import the MessageBox component
import { StyledInput } from './BaseUiComponents';
import { Monster } from './MonsterGame';

const NumberInput = ({
    gameState,
    updateGameState,
    statKey,
    label,
    initialCost = 10,
    increaseAmount = 10,
    costIncreaseFactor = 10,
    setMessage
  }) => {
    const [currentCost, setCurrentCost] = useState(initialCost);
  
    const handleIncrease = () => {
        if (gameState.gold >= currentCost) {
          const updatedMonster = new Monster({
            ...gameState.playerMonster,  // Spread the existing stats
            [statKey]: gameState.playerMonster[statKey] + increaseAmount // Update the specific stat
          });
      
          updateGameState({
            gold: gameState.gold - currentCost,
            playerMonster: updatedMonster  // Use the new Monster instance
          });
      
          setCurrentCost(currentCost + costIncreaseFactor); // Increase the cost after each upgrade
        } else {
          setMessage("💀 Not enough gold! 💀");
        }
      };
      
  
    return (
      <div className="stat-item">
        <label>{label}</label>
        <StyledInput
          gameState={gameState}
          updateGameState={updateGameState}
          statKey={statKey}
        />
        <button onClick={handleIncrease} disabled={gameState.gold < currentCost}>
          + <span>(+{increaseAmount})</span>
        </button>
        <span>Cost: ${currentCost}</span>
      </div>
    );
  };


  const BaseStats = ({ gameState, updateGameState, setMessage }) => {
    return (
      <div id="stat-sheet">
        <div id="gold-display">💰 Gold: {gameState.gold}</div>
        <div className="input-grid">
          <StyledInput
            gameState={gameState}
            updateGameState={updateGameState}
            statKey="name"
            placeholder="Monster Name"
          />
          <NumberInput
            gameState={gameState}
            updateGameState={updateGameState}
            statKey="hp"
            label="💀 HP"
            setMessage={setMessage}
          />
          <NumberInput
            gameState={gameState}
            updateGameState={updateGameState}
            statKey="dmg"
            label="⚔️ Damage"
            initialCost={15}
            costIncreaseFactor={15}
            setMessage={setMessage}
          />
          <NumberInput
            gameState={gameState}
            updateGameState={updateGameState}
            statKey="block"
            label="🛡️ Block"
            initialCost={15}
            costIncreaseFactor={15}
            setMessage={setMessage}
          />
          <NumberInput
            gameState={gameState}
            updateGameState={updateGameState}
            statKey="mana"
            label="🧠 Mana"
            initialCost={50}
            costIncreaseFactor={50}
            setMessage={setMessage}
          />
          <NumberInput
            gameState={gameState}
            updateGameState={updateGameState}
            statKey="spellDmg"
            label="🔮 Spell DMG"
            initialCost={5}
            costIncreaseFactor={5}
            setMessage={setMessage}
          />
        </div>
      </div>
    );
  };
  
const MonsterCreator = ({ gameState, updateGameState, setMessage }) => {
  const [activeTab, setActiveTab] = useState('base-stats');

  return (
    <div>
      
      <div>
        <button onClick={() => setActiveTab('base-stats')}>Base Stats</button>
        <button onClick={() => setActiveTab('weapons')}>Weapons</button>
        <button onClick={() => setActiveTab('spells')}>Spells</button>
        <button onClick={() => setActiveTab('abilities')}>Abilities</button>
      </div>
      {activeTab === 'base-stats' && (
        <BaseStats gameState={gameState} updateGameState={updateGameState} setMessage={setMessage} />
      )}
    </div>
  );
};

export default MonsterCreator;
