//MonsterBattle.js
import React, { useState, useEffect } from 'react';
import { Monster } from './MonsterGame';

const MonsterStats = ({ monster }) => {
    if (!monster) {
        return <div>No monster available</div>;
    }

    return (
        <div className="monster-stats">
            <h3>{monster.name}</h3>
            <p>HP: {monster.hp}/{monster.maxHp}</p>
            <div className="hp-bar">
                <div className="hp-bar-inner" style={{ width: `${(monster.hp / monster.maxHp) * 100}%` }}></div>
            </div>
            <p>🧠 {monster.mana}</p>
        </div>
    );
};


const BattleLog = ({ log }) => (
    <div id="battle-log">
        {log.map((entry, index) => (
            <div key={index} className="battle-round">
                <strong>🔄 Round {entry.round}</strong>
                <div className="player-action">{entry.playerAction}</div>
                <div className="opponent-action">{entry.opponentAction}</div>
            </div>
        ))}
    </div>
);

const MonsterBattle = ({ gameState, updateGameState, setMessage, setActiveTab }) => {
    const [battleLog, setBattleLog] = useState([]);
    const [battleRound, setBattleRound] = useState(0);

    const { playerMonster, enemyMonster } = gameState;

    useEffect(() => {
        if (battleRound > 0) {
            battleTurn();
        }
    }, [battleRound]);


    const startBattle = () => {
        setBattleRound(1);
        setBattleLog([]);
        setMessage("⚔️ The battle begins! ⚔️");
        console.log("Battle started");
    };

    const battleTurn = () => {
        console.log("Battle turn", battleRound);
        const playerAction = playerMonster.castSpell(enemyMonster);
        const opponentAction = enemyMonster.castSpell(playerMonster);

        const newLog = [...battleLog, {
            round: battleRound,
            playerAction: `${playerAction.type === 'spell' ? '🔮' : '⚔️'} ${playerAction.damage} 💥`,
            opponentAction: `${opponentAction.type === 'spell' ? '🔮' : '⚔️'} ${opponentAction.damage} 💥`
        }];

        setBattleLog(newLog);
        updateGameState({ playerMonster, enemyMonster });

        if (!playerMonster.isAlive() || !enemyMonster.isAlive()) {
            endBattle();
        } else {
            setTimeout(() => setBattleRound(battleRound + 1), 1000);
        }
    };

    const endBattle = () => {
        console.log("Battle ended");
        let reward;
        if (playerMonster.isAlive() && !enemyMonster.isAlive()) {
            reward = Math.floor(80 * gameState.difficulty);
            setMessage(`💀 Victory! You plundered ${reward} gold. 💀`);
        } else if (!playerMonster.isAlive()) {
            reward = Math.floor(20 * gameState.difficulty);
            setMessage(`💀 Defeat! You scavenged ${reward} gold. 💀`);
        } else {
            setMessage("💀 The battle ends in a draw. 💀");
        }

        setTimeout(() => {
            setBattleRound(0);
            updateGameState({
                gold: gameState.gold + (reward || 0),
                difficulty: playerMonster.isAlive() ? gameState.difficulty + 0.2 : gameState.difficulty,
                playerMonster: new Monster({ ...playerMonster, hp: playerMonster.maxHp, mana: 50 }),
                enemyMonster: new Monster({ ...createEnemyMonster(gameState) })
            });
            setActiveTab('creator');
        }, 800);
    };

    const createEnemyMonster = (gameState) => {

        return new Monster({
            name: 'Enemy Monster',
            hp: Math.floor(80 * gameState.difficulty),
            dmg: Math.floor(8 * gameState.difficulty)
        });
    }

    return (
        <div id="battle-view">
            <h2>⚔️ Battlefield of Doom vs Level {gameState.difficulty} Monster ⚔️</h2>
            <div className="battle-arena">
                <MonsterStats monster={playerMonster} />
                <MonsterStats monster={enemyMonster} />
            </div>
            <BattleLog log={battleLog} />
            {battleRound === 0 ? (
                <button id="start-battle" onClick={startBattle}>⚔️ BATTLE ⚔️</button>
            ) : (
                <button onClick={endBattle}>🏁 Retreat</button>
            )}
        </div>
    );
};

export default MonsterBattle;